import { useState,useEffect } from 'react'
import openSocket from 'socket.io-client'
import Axios from './axios'

function App() {
  const [users, setUsers] = useState([])

  useEffect(() => {
    Axios.get('/').then((res) => {
      setUsers([...res.data.users])
    })
    
    const socket = openSocket('https://socketio-test-first.herokuapp.com/')
    socket.on('newUser', data => {
      console.log(data);
      if (data.action == 'newUser') {
        Axios.get('/').then((res) => {
          setUsers([...res.data.users])
        })
        
      }
    })
  },[])
  
  const userSaveHandler = (e) => {
    e.preventDefault()
    Axios.post('/', {
      user: e.target.user.value
    }).then(res => {
      e.target.user.value = ''
    })
    
    
  }

  return (
    <div>
      <ul>
        {users.map(user => {
          return (
            <li key={user}>{ user }</li>
          )
        })}
      </ul>
      <form action="" onSubmit={userSaveHandler}>
        <input type="text" name='user' />
        <button>Submit</button>
      </form>
    </div>
  );
}

export default App;
